.app__aboutus {
    position: relative;
}

.app__aboutus .headtext__cormorant {
    font-family: var(--font-base);
    color: #e100ff;
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 117px;
    font-size: 65px;
}

.app__aboutus .custom__button{
    border: 2px solid #240621;
    padding: 14px 28px;
    background-color: #0040ff;
    font-size: larger;
    -webkit-text-stroke: #ffffff;
    -webkit-text-stroke-width:medium;
    border-radius: 5em;
    outline-width: 2em;
    color:rgb(0, 0, 0);
    cursor: pointer;
}

.app__aboutus .custom__button:hover {
    background-color: #000000;
    color:#0040ff;
    border: 2px solid #0040ff;
    -webkit-text-stroke:thin;
}

.app__aboutus-overlay {
    
    inset: -30%;
}

.app__aboutus-overlay img {
    position: absolute;
    top: 5%;
    right: 10%;
    width: 30%;
    z-index: 0;
}

.app__aboutus-overlay_T img {
    position: absolute;
    top: 5%;
    left: 10%;
    width: 30%;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: right;
}

.app__aboutus-content_knife {
    margin: 2rem 4rem;
}

.app__aboutus-content_knife img {
    height: 910px;
}

.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
}

.app__aboutus-content_about p,
.app__aboutus-content_history p {
    margin: 2rem 0;
    color: var(--color-white);
}

@media screen and (min-width: 2000px) {
    .app__aboutus-content_knife img {
        height: 1110px;
    }

    .app__aboutus-content_about p,
    .app__aboutus-content_history p {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 900px) {
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_knife {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img {
        width: 80%;
        height: 320px;
    }
    .app__aboutus-overlay_T img {
        width: 80%;
        height: 320px;
    }
}