.app__specialMenu {
    flex-direction: column;
    background: #000000;
}

.app__specialMenu .headtext__cormorant {
    font-family: var(--font-base);
    color: #e100ff;
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 117px;
    font-size: 65px;
}

.app__specialMenu .custom__button{
    border: 2px solid #240621;
    padding: 14px 28px;
    background-color: #e100ff;
    font-size: larger;
    -webkit-text-stroke: #ffffff;
    -webkit-text-stroke-width:medium;
    border-radius: 5em;
    outline-width: 2em;
    color:rgb(0, 0, 0);
    cursor: pointer;
}

.app__specialMenu .custom__button:hover {
    background-color: #000000;
    color:#e100ff;
    border: 2px solid #e100ff;
    -webkit-text-stroke:thin;
}

.app__specialMenu-title {
    margin-bottom: 2rem;
    text-align: center;
}

.app__specialMenu-menu {
    width: 100%;
    margin: 2rem 0;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}

.app__specialMenu-menu_heading {
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 55px;
    line-height: 58.5px;
    letter-spacing: 0.04em;
    color: #0040ff;
}

.app__specialMenu-menu_wine,
.app__specialMenu-menu_cocktails {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__specialMenu-menu_img {
    width: 410px;
    margin: 0 2rem;
}

.app__specialMenu-menu_img img {
    width: 100%;
    height: auto;
}

.app__specialMenu_menu_items {
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
    width: 100%;
}

@media screen and (min-width: 2000px) {
    .app__specialMenu-menu_img {
        width: 650px;
    }

    .app__specialMenu-menu_img img {
        height: 920px;
    }
}

@media screen and (max-width: 1150px) {
    .app__specialMenu-menu {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .app__specialMenu-menu_img {
        margin: 3rem 0;
    }
}

@media screen and (max-width: 650px) {
    .app__specialMenu-menu_img {
        width: 100%;
    }

    .app__specialMenu-menu_heading {
        font-size: 35px;
        line-height: 48.5px;
    }
}